.searchBox {
  display: flex;
  flex-direction: column;
  align-items: center;

  input[type="search"] {
    font-size: 1.5rem;
    padding: 0.5rem;
    :focus {
      background-color: red;
    }
  }

  input[type="search"]:focus {
    box-shadow: 0 0 20px yellow;
    outline-color: red;
  }
}
